.about {
    background-color: var(--dark-brown);
}

.tabs {
    @include tabs
}

.tabs {
    & .tabs__nav-btn {
        @include high-tablet {
            padding: 10px;
        }

        margin-right: 10px;
    }

    &__nav {
        flex-wrap: wrap;
        @include flex-all-sb;
        justify-content: flex-start;
        margin-bottom: 50px;

        &-btn {
            position: relative;
            margin-bottom: 20px;
            transition: border .3s ease-in-out, transform .3s ease-in-out, padding .3s ease-in-out;

            cursor: pointer;

            &::after {
                content: "";
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                border: solid 3px var(--accent-color);
                border-radius: 13px;
                z-index: 10;
                opacity: 0;
                transition: z-index .3s ease-in-out, opacity .3s ease-in-out;
            }

            &:hover:not(:active)::after {
                opacity: 1;
            }

            &:active {
                background-color: var(--focus-color);
                color: var(--light-color);
            }
        }
    }
}

.product {

    &__list {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(19em, 1fr));
        grid-gap: 0.5em;
        margin: 0 auto;
    }

    &__item {
        min-height: 200px;
        background-color: var(--light-color);
        display: inline-block;
        vertical-align: top;
        margin: 1em 0.5em;
        padding: initial;
        max-width: none;
        border-radius: 15px;
        background-color: var(--white-color);

        &.brusnika {
            max-width: 600px;
        }
    }

    &__card {
        display: flex;
        position: relative;
        flex-direction: column;
    }

    &__sticker {
        position: absolute;
        top: 5px;
        right: 5px;
        color: var(--light-color);
        width: 150px;
        height: 50px;
        background: linear-gradient(to left, darkred, red, darkred);
        clip-path: polygon(0 50%, 20% 0, 100% 0, 100% 100%, 20% 100%);
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
        font-weight: 600;
        text-indent: 30px;
    }

    &__sale {
        color: var(--accent-color);
        font-weight: 600;
        padding-left: 20px;
    }

    &__price {
        font-weight: 600;
        font-size: 20px;

        &-sale {
            position: relative;

            &::before {
                border-bottom: 3px solid red;
                position: absolute;
                content: "";
                width: 40%;
                height: 44%;
                transform: rotate(-18deg);
            }
        }
    }


    &__image {
        height: 300px;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        border-radius: 15px 15px 0 0;

        &-1 {
            background-image: url(../img/product_1.jpg);
        }

        &-2 {
            background-image: url(../img/product_2.jpg);
        }

        &-3 {
            background-image: url(../img/product_3.jpg);
        }

        &-4 {
            background-image: url(../img/product_4.jpg);
        }

        &-5 {
            background-image: url(../img/product_5.jpg);
        }

        &-6 {
            background-image: url(../img/product_6.jpg);
        }

        &-7 {
            background-image: url(../img/product_brusnika.jpg);
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 20px;
        flex-grow: 1;
    }

    &__descr {
        margin: 0;
        margin-bottom: 15px;
    }

    &__price {
        margin: 0;
        margin-top: auto;
    }


}

.product__item .product__card {
    height: 100%;
}