.footer {
    padding: 40px 0;
    background-color: var(--dark-color);


    &__logo {
        display: inline-block;
        margin-bottom: 5px;
    }

    &__container {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;

        @include mobile {
            flex-direction: column;
            align-items: center;
        }
    }

    &__copy {
        font-weight: 400;
        font-size: 16px;
        line-height: 160%;
        color: rgba(255, 255, 255, 0.7);

        @include mobile {
            margin-bottom: 30px;
            text-align: center;
        }
    }

    &__right-descr {
        color: var(--light-color);
    }


    &__social {
        display: flex;
        &-item {
            &:not(:last-child) {
                margin-right: 10px;
            }
        }
        &-icon {
            width: 54px;
            height: 54px;
            fill: white;
            transition: fill .3s ease-in-out;

            &:hover {
                fill: var(--accent-color);
            }

            &:focus-visible {
                fill: var(--brown-color);
            }

            &:active {
                fill: var(--focus-color);
            }

        }
    }

    &__descr {
        color: var(--light-color);
        margin-bottom: 20px;
    }

    &__center {
        @include mobile {
            margin-bottom: 20px;
        }
    }
}