.order {
    padding-bottom: 70px;
    background-color: var(--focus-color);
    background-size: cover;
    background-position: bottom center;
    border-top: 2px solid var(--light-color);

    &__container {
        @include high-tablet {
            display: flex;
            flex-direction: column;
        }

        display: grid;
        grid-template-columns: repeat(12, 1fr);

    }

    &__left {
        @include high-tablet {
            @include flex-all-center;
            flex-direction: column;
            margin-bottom: 30px;
            grid-column: 12 span;
        }

        grid-column: 5 span;
        padding: 0 20px;

    }

    &__title {
        text-align: center;
        margin: 0;
        margin-bottom: 20px;
        font-weight: 600;
        font-size: 36px;
        line-height: 130%;
        color: var(--light-color);
    }

    &__subtitle {
        margin: 0;
        color: var(--light-color);
    }

    &__descr {
        &-adress {
            @include mobile {
                text-align: center;
            }

            position: relative;
            padding-top: 20px;
            padding-left: 20px;

            &::before {
                @include mobile {
                    display: none;
                }

                position: absolute;
                content: "";
                top: 32px;
                left: 5px;
                height: 7px;
                width: 7px;
                border-radius: 100%;
                background-color: var(--light-color);
            }

        }

    }

    &__text {
        display: flex;
        flex-direction: column;
    }

    &__phone {
        @include mobile {
            margin: 0 auto;
        }

        margin: 0;
        padding-left: 24px;
        font-family: TTFirsNeue;
        font-size: 20px;
        font-weight: 600;
        line-height: 21px;
        color: var(--light-color);
        background-image: url("../img/call-grey.svg");
        background-size: 23px 23px;
        background-position: left center;
        background-repeat: no-repeat;
        transition: color .3s ease-in-out,
        background-image .3s ease-in-out;

        &:hover {
            color: var(--brown-color);
            background-image: url("../img/call.svg");
        }
    }

    &__right {

        @include high-tablet {
            @include flex-h-center;
            grid-column: 12 span;
        }

        @include mobile {
            padding-right: 20px;
        }

        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        grid-column: 7 span;
        padding: 0 20px;

    }

    &__form {
        margin: 0 auto;
        max-width: 500px;
    }

    &__work {
        margin-bottom: 20px;
        padding-top: 30px;
    }
}