.recipes {
    padding-bottom: 70px;
    background-color: var(--dark-brown);

    &__nav {
        padding-bottom: 65px;
    }

    &__wrapper {
        padding-left: max(15px, calc((100% - var(--content-width)) / 2 ));
        padding-bottom: 20px;
    }

    &__btns {
        position: relative;
        @include mobile {
          display: none;
        }
      }

    &__slider-btn {
        @include bg-cover;

        width: 40px;
        height: 40px;


        &--prev {
            background-color: var(--accent-color);

            &:focus {
                background-color: var(--focus-color);
                border: 2px solid var(--dark-brown);
            }

            &:hover {
                border: 2px solid var(--dark-brown);
            }

            &:active {
                border: 2px solid var(--accent-color);
            }

        }

        &--next {
            background-color: var(--accent-color);
           
            &:focus {
                background-color: var(--focus-color);
                border: 2px solid var(--dark-brown);
            }

            &:hover {
                border: 2px solid var(--dark-brown);
            }

            &:active {
                border: 2px solid var(--accent-color);
            }

        }

        &:not(:last-child) {
            margin-right: 15px;
        }       
    }

    &__item {
        display: flex;
        flex-grow: 1;
        flex-direction: column;
        align-items: flex-start;
        background-size: cover;
    }

    &__btn {
        margin: 4px;
        margin-top: auto;
        font-weight: 400;
        font-size: 18px;
        line-height: 160%;
        padding: 5px 15px;
        min-width: 100px;
        z-index: 1;
      }

    &__image {
        @include mobile {
            height: 300px;
        }
        margin-bottom: 20px;
        border-radius: 20px;
        height: 400px;
       
    }
}

.swiper-slide {
    @include mobile {
        width: 290px;
    }
    width: 360px;
}

