// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "OpenSans", sans-serif;
  --content-width: 1170px;
  --content-fluid-width: 1790px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-fluid-width: calc(var(--content-fluid-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --focus-color:  #9c1a1f;
  --accent-color: #d52027;
  --brown-color: #35170C;
  --dark-brown: #1C1C1C;
  --hell-brown: #725839;
  --dark-color: #141024;
  --white-color: rgba(255, 255, 255, 0.973);
  --brown-colors: #1c1c1cc4; 
}
