.graph-modal__close {
    right: 40px;
    top: 40px;
    width: auto;
    height: auto;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--text-color);

    @include mobile {
        right: 20px;
        top: 20px;
    }
}

.graph-modal {
    &::before {
        @include tablet {
            display: none;
        }
    }
}

.popup {
    padding: 40px;
    width: 100%;
    max-width: 940px;

    &__top {
        width: 90%;
        margin-bottom: 40px;
        padding-top: 40px;
    }

    &__bottom {
        @include mobile {
            display: inline;
        }

        display: flex;

        &-left {
            @include mobile {
                max-width: 100%;
            }

            max-width: 40%;
            margin-right: 40px;
        }

        &-right {
            @include mobile {
                max-width: 100%;
            }

            max-width: 50%;
        }
    }

    &__image {
        @include mobile {
            width: 100%;
        }

        .imagess {
            border-radius: 15px;
        }
    }


    &__text {
        margin: 0;
        margin-bottom: 5px;
        font-weight: 400;
        font-size: 20px;
        line-height: 160%;
        color: var(--dark-color);
    }


    @include small-tablet {
        padding-top: 80px;
    }

    @include mobile {
        padding: 20px;
        padding-top: 40px;
    }
}

.burger {
    display: none;

    @include high-tablet {
        display: block;
    }

    &-close {
        display: none;

        @include high-tablet {
            display: block;
        }
    }
}