.nav {
    &__list {
        @include flex-v-center;

        @include high-tablet {
            @include flex-all-center;
            flex-direction: column;
            width: 100%;
            height: 100%;
        }

        @include mobile {
            padding: 0 10px;
        }
    }

    &__item {
        &:not(:last-child) {
            margin-right: 40px;

            @include high-tablet {
                margin-right: 0;
                margin-bottom: 30px;
            }
        }

        @include mobile {
            text-align: center;
        }

    }

    &__link {
        position: relative;
        font-weight: 400;
        font-size: 20px;
        line-height: 160%;
        color: var(--light-color);
        transition: color .3s ease-in-out;
        z-index: 1;

        &::before {
            content: "";
            position: absolute;
            top: -5px;
            bottom: -5px;
            left: -8px;
            right: -8px;
            background-color: var(--focus-color);
            border-radius: 5px;
            opacity: 0;
            z-index: -1;
            transition: background-color .3s ease-in-out, opacity .3s ease-in-out;
        }

        &:focus:not(:active)::before {
            opacity: 1;
        }

        &:hover:not(:focus) {
            color: var(--accent-color);
            border: none;
            background-color: transparent;
        }

        &:active:not(:focus-visible) {
            background-color: transparent;
            color: var(--focus-color);
        }


        @include high-tablet {
            font-weight: 600;
            font-size: 30px;
            line-height: 160%;
        }
    }

    @include high-tablet {
        position: fixed;
        left: 0;
        top: 0;
        z-index: 500;
        overflow-y: auto;
        width: 100%;
        height: 100vh;
        background-color: var(--dark-color);
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }
}

.menu--active {
    opacity: 1;
    visibility: visible;
}