.hero {
    @include small-tablet {
        background-image: url("../img/bg-phone-mobile.jpg");
    }

    @include mobile {
        background-image: url("../img/bg-mobile.jpg");
    }


    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: var(--dark-brown);
    background-image: url("../img/bg-phone.jpg");

    &__container {
        @include flex;
        padding-top: 200px;
        padding-bottom: 100px;
    }

    &__content {
        max-width: 516px;
    }

    &__title {
        margin: 0;
        margin-bottom: 50px;
        font-weight: 600;
        font-size: 70px;
        line-height: 160%;
        color: var(--light-color);
        text-shadow: var(--brown-color) 4px 6px 5px;

        &-text {
            display: block;
            max-width: 90%;
            font-weight: 400;
            font-size: 20px;
            line-height: 160%;
            text-transform: initial;
            text-shadow: var(--brown-color) 2px 4px 3px;

            @include high-tablet {
                max-width: none;
            }
        }

    }

}