.delivery {
    padding-bottom: 70px;
    background-color: var(--focus-color);


    &__name {
        text-align: center;
    }

    &__descr {
        margin: 0;
        padding-top: 20px;
        color: var(--light-color);
    }

    &__nav {
        padding-bottom: 65px;
    }

    &__wrapper {
        padding-left: max(15px, calc((100% - var(--content-width)) / 2));
    }

    &__btns {
        position: relative;

        @include mobile {
            display: none;
        }
    }

    &__slider-btn {
        @include bg-cover;
        width: 40px;
        height: 40px;

        &--prev {      
            background-color: var(--dark-brown);

            &:focus {
                background-color: var(--brown-colors);
                border: 2px solid var(--accent-color);
            }

            &:hover {
                background-color: var(--brown-colors);
                border: 2px solid var(--focus-color);
            }

            &:active {
                border: 2px solid var(--light-color);
            }

        }
    

        &--next {
            
            background-color: var(--dark-brown);

            &:focus {
                background-color: var(--brown-colors);
                border: 2px solid var(--accent-color);
            }

            &:hover {
                background-color: var(--brown-colors);
                border: 2px solid var(--focus-color);
            }

            &:active {
                border: 2px solid var(--light-color);
            }
        }

        &:not(:last-child) {
            margin-right: 15px;
        }
    }
}