.btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    padding: 25px 30px;
    min-width: 260px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    text-align: center;
    color: var(--light-color);
    background-color: var(--accent-color);

    &--dark {
        background-color: var(--dark-color);
    }

    &--stroke {
        position: relative;
        transition: border .3s ease-in-out, transform .3s ease-in-out, padding .3s ease-in-out;

        &::before {
            content: "";
            display: block;
            position: absolute;
            top: -5px;
            left: -5px;
            right: -5px;
            bottom: -4px;
            border: solid 2px var(--focus-color);
            border-radius: 5px;
            z-index: 10;
            opacity: 0;
            transition: z-index .3s ease-in-out, opacity .3s ease-in-out;
        }

        &::after {
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            border: solid 3px var(--dark-brown);
            border-radius: 5px;
            opacity: 0;
            transition: z-index .3s ease-in-out, opacity .3s ease-in-out;
        }

        &:hover:not(:active)::after {
            opacity: 1;
        }

        &:focus::before {
            opacity: 1;
        }

        &:active::before {
            opacity: 0;
        }

        &:active {
            background-color: var(--focus-color);
        }

    }

    &-common-prev {
        position: absolute;
        top: 0;
        left: 0;
        width: 40px;
        height: 40px;
        border: 2px solid transparent;
        border-radius: 50%;
        transition: border .3s ease-in-out, background-color .3s ease-in-out;

        &.swiper-button-disabled {
            opacity: 0.5;
            pointer-events: none;
        }

        &::after {
            content: '';
            position: absolute;
            top: 12px;
            left: 13px;
            border-left: solid 2px var(--light-color);
            border-top: solid 2px var(--light-color);
            width: 12px;
            height: 12px;
            transform: rotate(-45deg);
            transition: transform .3s ease-in-out;
        }
    }

    &-common-next {

        @include high-tablet {
            left: 7%;
        }

        @include small-tablet {
            left: 9%;
        }
        
        position: absolute;
            top: 0;
            left: 5%;
            width: 40px;
            height: 40px;
            border: 2px solid transparent;
            border-radius: 50%;
            transition: border .3s ease-in-out, background-color .3s ease-in-out;

            &.swiper-button-disabled {
                opacity: 0.5;
                pointer-events: none;
            }

            &::after {
                content: '';
                position: absolute;
                top: 12px;
                left: 10px;
                border-left: solid 2px var(--light-color);
                border-top: solid 2px var(--light-color);
                width: 12px;
                height: 12px;
                transform: rotate(135deg);
                transition: transform .3s ease-in-out;
            }
    }
}